import React,{useState} from 'react';
import {Link} from 'react-router-dom';
import {FaBars, FaTimes} from 'react-icons/fa';
import './Navbar.css';
import logo from '../images/logo.svg'; 

function Navbar(){
	const [click, setClick] = useState(false);

	const handleClick = () => setClick(!click);
	const closeMobileMenu = () => setClick(false);

	return(
		<>
			<div className="navbar">
				<div className="navbar-container container">
					<Link to="/" className="navbar-logo" onClick={closeMobileMenu}>
						<img src={logo} alt="Markdown Live preview logo"/>
					</Link>
					
					<div className = "menu-icon" onClick={handleClick}>
						{click ? <FaTimes /> : <FaBars />}
					</div>

					<ul className={click ? 'nav-menu active' :'nav-menu'}>
						<li className="nav-item">
							<Link to='/Markdown-Live-Preview/styleGuide' className="nav-links" onClick={closeMobileMenu}> 
								Style Guide 
							</Link>
						</li>
						
						<li className="nav-item">
							<Link to="/Markdown-Live-Preview" className="nav-links" onClick={closeMobileMenu}>
								Live Preview
							</Link>
						</li>
					</ul>
				</div>
			</div>
		
		</>
	);
}


export default Navbar;