import React from 'react';
import './styleGuide.css';

function styleGuide() {
	return (
		<>
			<div className = "rectangleContainer">
				<div className="heading"> Markdown Style guide </div>
				<div className="desc">A quick reference for syntax</div> 
			</div>

			<div className = "container">
				<div className = "heading">Heading</div>
				<div className = "syntax"> # This is heading (H1) </div>
				<div className = "syntax"> ## This is heading (H2) </div>
				<div className = "syntax"> ### This is heading (H3) </div>

				<div className = "heading">Bold</div>
				<div className = "syntax"> **This text will be bold** </div>

				<div className = "heading">Italic</div>
				<div className = "syntax"> *This text will be italic* </div>

				<div className = "heading">Strikethrough</div>
				<div className = "syntax">~~This text will be striked through~~</div>

				<div className = "heading">Ordered list</div>
				<div className = "syntax"> 1. First Item</div>
				<div className = "syntax"> 2. Second Item</div>
				<div className = "syntax"> 3. Third Item</div>

				<div className = "heading">Unordered list</div>
				<div className = "syntax"> - First Item</div>
				<div className = "syntax"> - Second Item</div>
				<div className = "syntax"> - Third Item</div>

				<div className = "heading">Code</div>
				<div className = "syntax">`Code blocks should be embeded here`</div>

				<div className = "heading">Horizontal Line</div>
				<div className = "syntax">---</div>

				<div className = "heading">Link</div>
				<div className = "syntax">[link](https:www.harikabv.com)</div>

				<div className = "heading">Code Block</div>
				<div className = "syntax">``` int x = 5; System.out.println(x); ```</div>

								
			</div>
		</>
	);
}

export default styleGuide;